.imgActions {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

.uploadImg {
  margin: auto;
  background-color: #febd69;
  border-radius: 4px;
  border: none;
  box-shadow: 0px 0px 5px black;
  cursor: pointer;
}

.submit {
  background-color: #febd69;
  padding: 5px;
  border-radius: 4px;
  border: none;
  box-shadow: 0px 0px 5px black;
  cursor: pointer;
  width: 10%;
  margin: auto;
}

.fa-chevron-right, .fa-chevron-left {
  cursor: pointer;
  color: black;
  font-size: 40px;
  opacity: 1;
}

.carousel-control-next, .carousel-control-prev {
  text-decoration: none;
  position: absolute;
  top: 25%;
}

.prodDetails {
  width: 100%;
  background-color: #F1F3F2;
  border: 1px #febd69 solid;
  box-shadow: 0px 0px 5px #febd69;
}

.prodDetails h2 {
  text-align: center;
  font-weight: bolder;
  color: black;
}

.prodDetails label {
  font-size: 14px;
  font-weight: 700;
  color: black;
}

.info {
  border: 1px #febd69 solid;
  box-shadow: 0px 0px 5px #febd69;
}

.invalid {
  border: red 2px solid;
  border-radius: 4px;
  content: "hi";
  background-image: url("../../../assets/images/icons/invalid.png");
  background-size: 35px 35px;
  background-position: right;
  padding-right: 40px;
  background-repeat: no-repeat;
}

.invalid:focus {
  border: 2px red solid;
  box-shadow: 0px 0px 5px red;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

